"use strict";

// 下拉菜单和购物车
$(function () {
  var hide_timer = null;
  $("#shoplist").mousemove(function () {
    var _this = this;

    clearTimeout(hide_timer);
    hide_timer = setTimeout(function () {
      $(".shop-list").addClass("show-list");
      $(_this).css({
        cursor: "pointer"
      });
    }, 200);
  }); //定时器

  $("#shoplist").mouseleave(function () {
    // flag = true;
    clearTimeout(hide_timer);
    hide_timer = setTimeout(function () {
      $(".shop-list").removeClass("show-list");
    }, 200);
  });
  $(".shop-list").mousemove(function () {
    clearTimeout(hide_timer);
  });
  $(".shop-list").mouseout(function () {
    clearInterval(hide_timer);
    hide_timer = setInterval(function () {
      $(".shop-list").removeClass("show-list");
    }, 200);
  });
});
var str1 = "";
var str2 = "";
var htmlitem = "";
var html1 = "";
var html2 = "";
var timeout;
var timeout2; // 鼠标移入事件
// 进行防抖处理
// 数据的渲染

$(".title-wrappercont>.title-content>li>a").mouseover(function (e) {
  var _this2 = this;

  htmlitem = "";
  var target = e.srcElement || e.target;
  clearTimeout(timeout); // clearTimeout(timeout);
  //先让页面变干净更好的渲染

  timeout = setTimeout(function () {
    // console.log(str2)
    str2 = $(_this2).html();
    $(".category-wrapper").children().remove();
    loadnav();
    $("#sub").removeClass("sub-panel-wrapper-hide");
    $("#sub").addClass("sub-panel-wrapper-show"); // console.log(str2)
    // $(".category-wrapper .item").remove();

    $("#navli").removeClass("active"); // 对元素进行添加和移除class

    $(target).addClass("active"); // console.log(htmlitem)
  }, 200);
});
$(".title-wrappercont>.title-content li a").mouseout(function (e) {
  var target = e.srcElement || e.target;
  clearTimeout(timeout);
  clearTimeout(timeout2);
  timeout2 = setTimeout(function () {
    $(".category-wrapper").children().remove();
    $("#sub").removeClass("sub-panel-wrapper-show");
    $("#sub").addClass("sub-panel-wrapper-hide");
  }, 200);
  $(target).removeClass("active"); // $(".category-wrapper").remove();
}); //下拉列表中的事件

$(".nav-sub-container").mouseover(function () {
  clearTimeout(timeout2);
});
$(".nav-sub-container").mouseleave(function (e) {
  var target = e.srcElement || e.target;
  clearTimeout(timeout2);
  clearTimeout(timeout);
  $("#sub").removeClass("sub-panel-wrapper-show");
  $("#sub").addClass("sub-panel-wrapper-hide");
  $(target).removeClass("active"); // console.log(html)
});

function loadnav() {
  var options = {
    // http://303z9z3029.qicp.vip/
    url: "http://303z9z3029.qicp.vip/secon_nav",
    success: rendernav,
    dataType: "json"
  };
  ajax(options);
}

function rendernav(res) {
  // htmlitem = ""
  // console.log(res)
  res.forEach(function (item) {
    // console.log(item)
    // console.log(str2)
    // console.log(item.list.length);
    if (item.name === str2 && item.list.length === 1) {
      // console.log(item);
      item.list.forEach(function (item) {
        item.sub.forEach(function (item) {
          // console.log(item)
          html2 += "<li class=\"category-item\">\n                       <a\n                          data-url=\"https://www.smartisan.com/category/297?type=shop\"\n                          class=\"link\"\n                        >\n                       <img\n                         src=\"".concat(item.image, "\"\n                         class=\"picture\"\n                       />\n                       <span class=\"sub-title\">").concat(item.name, "</span></a>\n                     </li>\n                  ");
        }); // console.log(item.length);

        html1 += "\n         \n           <li class=\"item\">\n             <div class=\"container\">\n               <div class=\"title\">".concat(item.title, "</div>\n                <ul class=\"category-container\" style=\"width: 202px;\">\n                ").concat(html2, "\n                 </ul>\n                </div>\n             </li>\n          ");
        htmlitem += "".concat(html1);
        html1 = "";
        html2 = "";
      });
      $(".category-wrapper").html(htmlitem); // return false
    }

    if (item.name === str2 && item.list.length > 1) {
      var lisize = item.list.length;
      item.list.forEach(function (item) {
        item.sub.forEach(function (item) {
          html2 += "\n          <li  class=\"category-item\">\n            <a\n              data-url=\"\"\n              class=\"link\"\n              ><img src=\"".concat(item.image, "\"\n                class=\"picture\"\n              /><span  class=\"sub-title\"\n                >").concat(item.name, "</span\n              ></a>\n          </li> ");
        }); // console.log(item);

        html1 += "\n        <li class=\"item\">\n          <div class=\"container\">\n            <div class=\"title\">\n              ".concat(item.title, "\n            </div>\n            <ul class=\"category-container\" style=\"width: 202px;\">\n            ").concat(html2, "\n            </ul>\n          </div>\n        </li>");
        htmlitem += html1;
        html1 = "";
        html2 = "";
      });
      $(".category-wrapper").html(htmlitem);
    }
  });
} //banner


var swiper = new Swiper('.swiper-container', {
  spaceBetween: 30,
  effect: 'fade',
  fadeEffect: {
    crossFade: true
  },
  pagination: {
    el: '.swiper-pagination',
    clickable: true
  },
  autoplay: {
    delay: 4000,
    stopOnLastSlide: false,
    disableOnInteraction: true
  }
}); // 渲染界面

function load() {
  var options = {
    // http://303z9z3029.qicp.vip/
    url: "http://303z9z3029.qicp.vip/shot_list",
    success: render,
    dataType: "json"
  };
  ajax(options);
}

function render(res) {
  // console.log(res)
  var data = res.data; // console.log(data)

  var html = ""; // console.log(data)

  data.forEach(function (item) {
    // console.log(item)
    html += "\n     <div class=\"hot-list-duct\">\n    <figure class=\"item-cover\">\n      <img\n      data-original=\"".concat(item.image_url, "\"\n        lazy=\"loaded\"\n      />\n    </figure>\n    <article class=\"artivle\">\n      <h3>").concat(item.goods_name, "</h3>\n      <h5 class=\"txt-product-title\">\n       ").concat(item.short_name, "\n      </h5>\n    </article>\n    <aside class=\"item-attr-colors\">\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          data-original=\"https://resource.smartisan.com/resource/a95138cf41c70fbb689e0bcd785a362f.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer active\">\n        <img\n        data-original=\"https://resource.smartisan.com/resource/b86e1a8858c1445d3fd77a0dcba2eb5c.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n      <figure class=\"outer\">\n        <img\n          data-v-380b3c68=\"\"\n          data-original=\"https://resource.smartisan.com/resource/d0c2d694d76184aa8deabab5e8e951af.png?x-oss-process=image/resize,w_30/format,webp\"\n        />\n      </figure>\n    </aside>\n    \n    <article class=\"item-price\">\n      <span>\uFFE5").concat(item.nowsales.toFixed(2), "</span>\n      <span class=\"orignal-price\">\uFFE5").concat(item.befsales.toFixed(2), "</span>\n    </article>\n  </div>");
  });
  _("#hot-list").innerHTML = html;
  $("#hot-list img").lazyload({
    placeholder: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsQAAA7EAZUrDhsAAAANSURBVBhXYzh8+PB/AAffA0nNPuCLAAAAAElFTkSuQmCC",
    effect: "fadeIn"
  });
}

load();

